.checkbox-btn {
    display: inline-block;
    width: 16px;
    height: 16px;
    background: #fff;
    border: 1px solid #999;
    border-radius: 50%;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
}
.checkbox-btn .icon-ok {
    position: relative;
    display: none;
    width: 100%;
    height: 100%;
    fill: #fff;
    -webkit-transform: scale(0.6);
    transform: scale(0.6);
    vertical-align: top;
}
.checkbox-btn.checked {
    background-color: #ee7a23;
    border-color: #ee7a23;
}
.checkbox-btn.checked .icon-ok {
    display: inline-block;
}
.checkbox-btn.orange.checked {
    background-color: #ee7a23;
    border-color: #ee7a23;
}
/** header and footer **/
html {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}
*, *::after, *::before {
    -webkit-box-sizing: inherit;
    box-sizing: inherit;
}
.header {
    position: relative;
}
.footer {
    background-color: #f5f7fc;
    color: #333;
    position: relative;
}
.footer__wrap {
    max-width: 1280px;
    margin: 0 auto;
}
.footer__primary .footer__inner {
