}
.footer__inner {
    max-width: 1280px;
    margin: 0 auto;
    position: relative;
}
.footer__region {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
    left: 36px;
}
.footer__region span {
    color: #ada9a5;
}
.footer__secondary__nav {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
