/** header and footer end **/
.md-modal {
    position: fixed;
    top: 50%;
    left: 50%;
    width: 535px;
    height: auto;
    -webkit-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    visibility: hidden;
    z-index: 201;
}
.md-modal.md-modal-m {
    width: 620px;
}
.md-modal .md-close {
    position: absolute;
    top: 7px;
    right: 7px;
    width: 34px;
    height: 34px;
    -webkit-transform: scale(0.5);
    transform: scale(0.5);
    text-indent: -8000px;
}
.md-modal .md-close:before, .md-modal .md-close:after {
    position: absolute;
    top: 16px;
    left: -4px;
    content: "";
    width: 44px;
    height: 3px;
    background: #d7d8dd;
    -webkit-transition: -webkit-transform .5s ease-out;
    transition: -webkit-transform .5s ease-out;
    transition: transform .5s ease-out;
    transition: transform .5s ease-out, -webkit-transform .5s ease-out;
}
.md-modal .md-close:before {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
.md-modal .md-close:after {
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg);
}
.md-modal .md-close:hover:before, .md-modal .md-close:hover:after {
    -webkit-transition: -webkit-transform .3s ease-out;
    transition: -webkit-transform .3s ease-out;
    transition: transform .3s ease-out;
    transition: transform .3s ease-out, -webkit-transform .3s ease-out;
}
.md-modal .md-close:hover:before {
    -webkit-transform: rotate(-135deg);
    transform: rotate(-135deg);
}
.md-modal .md-close:hover:after {
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg);
}
.md-modal .md-tit {
    font-weight: 200;
    line-height: 1.8em;
    text-align: center;
}
.md-modal .md-modal-inner {
    padding: 60px 50px;
}
.md-modal .btn-wrap {
    margin-top: 20px;
    text-align: center;
    font-size: 0;
}
.md-modal .btn-wrap .btn {
    width: 45%;
    min-width: 80px;
    margin: 0 2.5%;
}
.md-modal .confirm-tips, .md-modal .alert-tips {
    font-size: 14px;
    font-weight: 200;
    text-align: center;
}
.md-modal .confirm-tips p, .md-modal .alert-tips p {
    line-height: 1.8em;
}
.md-modal .confirm-tips {
    min-height: 5.65em;
}
.md-modal .alert-tips {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    height: 10.25em;
    -ms-flex-align: center;
    -webkit-box-align: center;
    align-items: center;
}
.md-modal .alert-tips p {
    -webkit-box-flex: 1;
    -ms-flex: 1;
    flex: 1;
}
.md-modal.modal-normal .content-tit {
    margin-bottom: 20px;
    line-height: 20px;
    font-size: 16px;
    color: #333;
}
.md-modal .icon-status-ok {
    width: 20px;
    height: 20px;
    fill: #ee7a23;
    vertical-align: middle;
    margin-right: 15px;
}
.md-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5);
    z-index: 200;
}
.md-modal-transition .md-modal-inner {
    background: #fff;
    -webkit-transform: translateY(20%);
    transform: translateY(20%);
    opacity: 0;
    -webkit-transition: all .3s ease-out;
    transition: all .3s ease-out;
}
.md-modal-transition.md-show {
    visibility: visible;
}
.md-modal-transition.md-show .md-modal-inner {
    -webkit-transform: translateY(0);
    transform: translateY(0);
    opacity: 1;
    -webkit-transition: all .5s ease-out;
